<template>
  <div class="container">
    <h1>Projects</h1>
    <h3>Check out my <a href="https://github.com/DSchroederOSU">Github</a></h3>
    <projectobject></projectobject>
  </div>
</template>
<script>
import $ from 'jquery'
import projectobject from './ProjectObject.vue'

export default {
  name: 'projects',
  components: {
    projectobject
  },
  mounted: function () {
    $('#navlist').find('li').removeClass('list-item-dark').addClass('list-item-light')
  }
}
</script>

<style scoped>
.list-item-light{
  font-size: 1.1em;
  padding-left: 20px;
  margin-right: 20px;
  color: black;
}
.container {
  margin-top: 122px;
}
a {
  color: #54a0ff;
}
</style>



