<template>
  <div class="container mt-5">
    <div class="card m-3" v-for="item in items">
      <h5 class="card-header">{{ item.name }}</h5>
      <div class="card-body">
        <h5 class="card-title">Description:</h5>
        <p class="card-text">{{ item.description }}</p>
        <a v-bind:href="item.url" class="btn btn-primary">{{ item.url }}</a>
      </div>
    </div>
  </div>
</template>
<script>
import $ from 'jquery'
export default {
  name: 'projectobject',
  data () {
    return {
      items: null
    }
  },
  created () {
    let self = this
    let projects = ['SeniorCapstone', 'A-Knights-Tale', 'Brewery_API', 'CS450-Computer-Graphics']
    $.getJSON('https://api.github.com/users/' + 'DSchroederOSU' + '/repos?callback=?', function (data) {
      console.log(data.data[0])
      let toReturn = []
      data.data.forEach(x => {
        if (projects.indexOf(x.name) !== -1) {
          toReturn.push(x)
        }
      })
      self.setData(toReturn)
    })
  },
  methods: {
    setData (response) {
      this.items = response
      this.json = response
    }
  }
}
</script>



