<template>
  <div class="home">
    <div class="image-cover">
      <h1 class="parallax-title">{{ msg }}</h1>
      <parallax :speed-factor="0.4" breakpoint="(min-width: 80px)">
        <img class="image-cover" src="https://i.imgur.com/AI2HZED.jpg">
      </parallax>
    </div>
    <div id="triangle-container" class="triangle-container">
      <img id="profile-image" src="../assets/IMG_5086.jpg" width="400" height="400">
      <div id="rightTriangle" class="triangle triangle-right"></div>
      <div id="leftTriangle" class="triangle triangle-left"></div>
      <span>Web Developer</span>
    </div>
  </div>
</template>

<script>
import Parallax from 'vue-parallaxy'
import $ from 'jquery'
export default {
  name: 'home',
  components: {
    Parallax
  },
  mounted: function () {
    $('#navlist').find('li').removeClass('list-item-light').addClass('list-item-dark')
  },
  data () {
    return {
      msg: 'My name is Daniel Schroeder and I like to code.'
    }
  }
}
</script>
<style scoped>
.section {
  height: 1000px;
  width: 100%;
  background-color: white;
}
.image-cover {
  position: relative;
  background: rgba(0, 0, 0, 0.4);
  opacity: 1;
}
.parallax-title {
  position: absolute;
  top: 45%;
  left: 0;
  right: 0;
  padding: 20px;
  color: white;
  text-align: center;
}
.triangle-container {
	box-sizing: border-box;
	padding: 5%;
	position: relative;
}
.triangle {
	background: none;
	border: 0 solid transparent;
	height: 0;
	position: absolute;
	top: 0;
	width: 0;
}
.triangle-right {
  padding-right: 50px;
	border-right-color: #ECF0F1;
  border-right-width: 400px;
	border-top-width: 850px;
	right: 0;
}
.triangle-left {
  left: 0;
	border-left-color: #ECF0F1;
  border-left-width: 400px;
	border-bottom-width: 850px;
  z-index: -100;
}
#profile-image {
  margin-top: 50px;
  margin-left: 100px;
  position: relative;
  left: -580px;
}
</style>



