<template>
    <nav class="navbar navbar-expand-lg fixed-top p-5">
      <div class="collapse navbar-collapse justify-content-end" id="navbarNav">
        <ul id="navlist" class="nav navbar-nav ">
          <router-link to='/'>
            <li class="nav-item active">
              HOME
            </li>
          </router-link>
          <router-link to='/about'>
            <li class="nav-item">
              ABOUT
            </li>
          </router-link>
          <router-link to='/blogs'>
            <li class="nav-item">
              BLOG
            </li>
          </router-link>
          <router-link to='/projects'>
            <li class="nav-item">
              PROJECTS
            </li>
          </router-link>
        </ul>
      </div>
    </nav>
</template>
<script>
import $ from 'jquery'

export default {
  name: 'navigation',
  created: function () {
    window.onload = function () {
      $('#profile-image').removeClass('slide-out').addClass('slide-in')
      $('#triangle-container').find('span').removeClass('retract').addClass('pop-up')
      $('#triangle-container').find('#rightTriangle').removeClass('push-right-triangle').addClass('pull-right-triangle')
      $('#triangle-container').find('#leftTriangle').removeClass('push-left-triangle').addClass('pull-left-triangle')
      $('#navlist').find('li').removeClass('list-item-light').addClass('list-item-dark')
    }
  },
  mounted: function () {
    $('#navlist').find('li').removeClass('list-item-light').addClass('list-item-dark')
    window.onscroll = function () {
      let doc = document.documentElement
      let top = (window.pageYOffset || doc.scrollTop) - (doc.clientTop || 0)
      if (top > 300 && top < 800) {
        $('#profile-image').removeClass('slide-out').addClass('slide-in')
        $('#triangle-container').find('span').removeClass('retract').addClass('pop-up')
        $('#triangle-container').find('#rightTriangle').removeClass('push-right-triangle').addClass('pull-right-triangle')
        $('#triangle-container').find('#leftTriangle').removeClass('push-left-triangle').addClass('pull-left-triangle')
        $('#navlist').find('li').removeClass('list-item-light').addClass('list-item-dark')
      } else if (top >= 800) {
        $('#navlist').find('li').removeClass('list-item-dark').addClass('list-item-light')
      } else {
        $('#profile-image').removeClass('slide-in').addClass('slide-out')
        $('#triangle-container').find('span').removeClass('pop-up').addClass('retract')
        $('#triangle-container').find('#rightTriangle').removeClass('pull-right-triangle').addClass('push-right-triangle')
        $('#triangle-container').find('#leftTriangle').removeClass('pull-left-triangle').addClass('push-left-triangle')
        $('#navlist').find('li').removeClass('list-item-light').addClass('list-item-dark')
      }
    }
  }
}
</script>
<style>
.list-item-dark {
  font-size: 1.1em;
  padding-left: 20px;
  margin-right: 20px;
  color: white;
}
.list-item-dark:hover {
  color: black;
}
.list-item-light {
  font-size: 1.1em;
  padding-left: 20px;
  margin-right: 20px;
  color: black;
}
.list-item-light:hover {
  color: grey;
}
nav {
  margin-right: 5%;
}
.slide-out {
  -webkit-transition: 1s ease-in-out;
  -moz-transition: 1s ease-in-out;
  -o-transition: 1s ease-in-out;
  transition: 1s ease-in-out;
  -webkit-transform: translate(0em,0);
  -moz-transform: translate(0em,0);
  -o-transform: translate(0em,0);
  -ms-transform: translate(0em,0);
  transform: translate(0em,0);
}
.slide-in {
  -webkit-transition: 1s ease-in-out;
  -moz-transition: 1s ease-in-out;
  -o-transition: 1s ease-in-out;
  transition: 1s ease-in-out;
  -webkit-transform: translate(680px,0);
  -moz-transform: translate(680px,0);
  -o-transform: translate(680px,0);
  -ms-transform: translate(680px,0);
  transform: translate(680px,0);
}
.pop-up {
  -webkit-transition: 1s ease-in-out;
  -moz-transition: 1s ease-in-out;
  -o-transition: 1s ease-in-out;
  transition: 1s ease-in-out;
  -webkit-transform: translate(0px, 0px);
  -moz-transform:translate(0px, 0px);
  -o-transform: translate(0px, 0px);
  -ms-transform: translate(0px, 0px);
  transform: translate(0px, 0px);
}
.retract {
  -webkit-transition: 1.2s ease-in-out;
  -moz-transition: 1.2s ease-in-out;
  -o-transition: 1.2s ease-in-out;
  transition: 1.2s ease-in-out;
  -webkit-transform: translate(0em, 800px);
  -moz-transform: translate(0em, 800px);
  -o-transform: translate(0em, 800px);
  -ms-transform: translate(0em, 800px);
  transform: translate(0em, 800px);
}
.pull-right-triangle {
  -webkit-transition: 1s ease-in-out;
  -moz-transition: 1s ease-in-out;
  -o-transition: 1s ease-in-out;
  transition: 1s ease-in-out;
  -webkit-transform: translate(0px, 0px);
  -moz-transform: translate(0px, 0px);
  -o-transform: translate(0px, 0px);
  -ms-transform: translate(0px, 0px);
  transform: translate(0px, 0px);
}
.push-right-triangle {
  -webkit-transition: 1s ease-in-out;
  -moz-transition: 1s ease-in-out;
  -o-transition: 1s ease-in-out;
  transition: 1s ease-in-out;
  -webkit-transform: translate(200px, 0px);
  -moz-transform: translate(200px, 0px);
  -o-transform: translate(200px, 0px);
  -ms-transform: translate(200px, 0px);
  transform: translate(200px, 0px);
}
.pull-left-triangle {
  -webkit-transition: 1.2s ease-in-out;
  -moz-transition: 1.2s ease-in-out;
  -o-transition: 1.2s ease-in-out;
  transition: 1.2s ease-in-out;
  -webkit-transform: translate(0px, 0px);
  -moz-transform: translate(0px, 0px);
  -o-transform: translate(0px, 0px);
  -ms-transform: translate(0px, 0px);
  transform: translate(0px, 0px);
}
.push-left-triangle {
  -webkit-transition: 1s ease-in-out;
  -moz-transition: 1s ease-in-out;
  -o-transition: 1s ease-in-out;
  transition: 1s ease-in-out;
  -webkit-transform: translate(-400px, 0px);
  -moz-transform: translate(-400px, 0px);
  -o-transform: translate(-400px, 0px);
  -ms-transform: translate(-400px, 0px);
  transform: translate(-400px, 0px);
}
#triangle-container span {
  position: relative;
  display: block;
  top: -400px;
  left: 650px;
  font-size: 2em;
}
</style>



